<template>
    <transition name="modal">
    <div class="modal-mask">
      <div class="modal-wrapper">
        <div class="modal-container">
          <div class="modal-header">
            <slot name="header">
              {{  modalHeader   }}
            </slot>
          </div>

          <div class="modal-body">
            <slot name="body">
              {{  modalText   }}
            </slot>
          </div>

          <div class="modal-footer">
            <slot name="footer">
              <button class="modal-button" @click="$emit('close')">
                {{  modalFooter   }}
              </button>
            </slot>
          </div>
        </div>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: "Modal",
  props: ["modalHeader", "modalText", "modalFooter"]
};
</script>

<style scoped lang="scss">
.modal-mask {
  position: fixed;
  z-index: 9998;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.85);
  display: table;
  transition: opacity 0.3s ease;
}

.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}

.modal-container {
  width: 300px;
  margin: 0px auto;
  padding: 20px 30px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
}

.modal-header h3 {
  margin-top: 0;
  color: #42b983;
}

.modal-body {
  margin: 20px 0;
}

.modal-button {
  display: block;
  padding: 0.6rem;
  border: none;
  margin: 0.6rem auto;
  color: #fff;
  background-color: black;
  max-width: 100px;
  cursor: pointer;
  a {
    color: #fff;
    text-decoration: none;
  }
  &:hover {
    background-color: rgba(0, 0, 0, 0.85);
  }
}

.modal-enter {
  opacity: 0;
}

.modal-leave-active {
  opacity: 0;
}

.modal-enter .modal-container,
.modal-leave-active .modal-container {
  -webkit-transform: scale(1.1);
  transform: scale(1.1);
}
</style>
